<template>
    <div id="dashboard">
         <div class="row">
                <h3 class="center-align">Notes</h3>
                <div v-for="note in notes" v-bind:key ="note.id" class="card hoverable blue col m12">
                    <div class="card-content white-text">
                    <span class="card-title center-align">{{note.title}}</span>
                    </div>
                    <div class="card-action">
                        <div class="left">{{note.date}}</div>
                        <div class="right"><div class="chip">{{note.type}}</div>
                         <router-link  
                            v-bind:to="{name : 'view-note', params:{title:note.title}}">
                            <i class="fa fa-eye"></i>
                        </router-link>
                    </div>
                    </div>
                </div>
        </div>            
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name : 'dashboard',
    data(){
        return {
            notes: []
        }
    },
    created() {
        db.collection('notes').orderBy('type').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.data())
                
                const data = {
                    'id': doc.id,
                    'title': doc.data().title,
                    'content':doc.data().content,
                    'date':doc.data().date,
                    'type':doc.data().type
                }
                this.notes.push(data)
            })
        })
    }
}
</script>





