<template>
    <div id="view-note">
        <h3 class="center-align">View Notes</h3>
        <div class="row">
            <div class="col s12 m12">
            <div class="card hoverable blue">
                <div class="card-content  white-text">
                <span class="card-title center-align">{{title}}</span>
                <p class="right-align">{{date}}</p>
                <p>{{content}}</p>
                </div>
                <div class="card-action">
                    <router-link to="/">
                        <div class="right"><span class="arrow"><i class="fa fa-arrow-circle-left fa-2x" aria-hidden="true"></i></span></div>
                    </router-link>
                    <i  @click="deleteNote" class="fa fa-trash fa-2x"></i>
                </div>
                <div class="fixed-action-btn">
                    <router-link v-bind:to="{name:'edit-note',params:{title:title}}" class="btn-floating btn-large red">
                        <i class="fa fa-pencil"></i>
                    </router-link>
                </div>
            </div>
            </div>
        </div>     
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name : 'view-note',
        data(){
            return {
                title : null,
                type: null,
                content:null,
                date:null
            }
        },
        beforeRouteEnter (to, from, next) {
            db.collection('notes').where('title','==',to.params.title).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm=> {
                        vm.title=doc.data().title,
                        vm.content=doc.data().content,
                        vm.date = doc.data().date
                        vm.type = doc.data().type
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData(){
                db.collection('notes').where
                ('title','==',this.$.params.title).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        this.title = doc.data().title,
                        this.content= doc.data().content,
                        this.type = doc.data().type,
                        this.date = doc.data().date

                    })
                })
            },
            deleteNote() {
                if(confirm("Are you sure to delete note?")){
                db.collection('notes').where
                ('title','==',this.$route.params.title).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
                }
            }
        }
    }
</script>


