<template>
    <div id="new-note">
        <h3>Add Note</h3>
        <div class="row">
            <form @submit.prevent="saveNote" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="title" required>
                        <label>Title</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <textarea class="materialize-textarea" v-model="content" required> </textarea>
                        <label>Content</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="type" required>
                        <label>Type</label>
                    </div>
                </div>
                <button class="btn" type="submit">Add Note</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    console.log(new Date(Date.now()).toLocaleString())
    
    export default {
        name : 'new-note',
        data(){
            return {
                title : null,
                type: null,
                content:null,
                date: null 
            }
        },
        methods: {
            saveNote(){
                db.collection('notes').add({
                    title: this.title,
                    content: this.content,
                    type:this.type,
                    date:new Date(Date.now()).toLocaleString()
                })
                .then(docRef => this.$router.push('/'))
                .catch(error => console.log(err))
            }
        }
    }
</script>


