<template>
    <div id="edit-note">
        <h3 class="center-align">Edit Note</h3>
        <div class="row">
            <form @submit.prevent="updateNote" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="title" required>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <textarea class="materialize-textarea" v-model="content" required> </textarea>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="type" required>
                        
                    </div>
                </div>
                <button class="btn" type="submit">Add Note</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name : 'edit-note',
        data(){
            return {
                title : null,
                type: null,
                content:null,
                date: null 
            }
        },
        beforeRouteEnter (to, from, next) {
            db.collection('notes').where('title','==',to.params.title).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm=> {
                        vm.title=doc.data().title,
                        vm.content=doc.data().content,
                        vm.date = doc.data().date
                        vm.type = doc.data().type
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData(){
                db.collection('notes').where
                ('title','==',this.$.params.title).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        this.title = doc.data().title,
                        this.content= doc.data().content,
                        this.type = doc.data().type,
                        this.date = doc.data().date

                    })
                })
            },
            updateNote() {
                db.collection('notes').where
                ('title','==',this.$route.params.title).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc =>{
                        doc.ref.update({
                            title:this.title ,
                            content:this.content,
                            type:this.type,
                            date:new Date(Date.now()).toLocaleString()
                        })
                        .then(() => {
                            this.$router.push({name :'view-note',
                            params: {title: this.title}})
                        })
                    })
                })
            }
        }
    }
</script>


